<template>
  <div>
     <div style="position:relative">
        <div class="videoNameClass">
          {{videoName}}
        </div>
        <video
          :id="playerId"
          ref="video"
          controls
          class="video-js vjs-default-skin vjs-big-play-centered"
          x5-playsinline="true"
          playsinline="true"
          webkit-playsinline
        ></video>
      </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    player: null,
    playerId: "suma-player4",
    initOptions: {
      // playbackRates: [0.25, 0.5, 1.0, 1.5, 2.0], //
      autoplay: true, //true,
      muted: false, // 
      loop: false, // 
      preload: "auto", // auto,
      //language: 'zh-CN',
      aspectRatio: "16:7", // "16:9""4:3"
      fluid: true, // true
      // sources: [
      //   {
      //     type: "application/x-mpegURL",
      //     src: "http://27.36.62.161:8060/live/fourthgrade_1500.m3u8?ci=123" //url
      //   }
      // ],
      poster: "", //
      // width: document.documentElement.clientWidth,
      notSupportedMessage: "", //
      controlBar: {
        // currentTimeDisplay: true,
        // timeDivider: true,
        // durationDisplay: true,
        // remainingTimeDisplay: false,
        // fullscreenToggle: true, //
        // volumePanel: {
        //   inline: false
        // }
        // 
        children: [
          { name: "playToggle" }, // 
          {
            name: "volumePanel", // 
            inline: false // 
          },
          { name: "currentTimeDisplay" }, // 
          { name: "progressControl" }, // 
          { name: "durationDisplay" }, // 
          // {
          //   // 
          //   name: "playbackRateMenuButton",
          //   playbackRates: [0.5, 1, 1.5, 2, 2.5]
          // },
          { name: "FullscreenToggle" } // 
        ]
      },
      errorDisplay: true,
      // posterImage: false,
      bigPlayButton: true,
      textTrackDisplay: false,
    },
    height:0,
    videoName:''
  }),
  methods: {
    showChannelName(name){
      this.videoName = name
    },
    /**
     * HLS
     * liveUrl 
     * siAutoPlay 
     */
    playLiveWithHlsUrl(liveUrl, isAutoPlay = true) {
      if (this.player != null) {
        console.log("playLiveWithHlsUrl:" + liveUrl);
        // this.player.reset();
        this.player.autoplay(isAutoPlay);
        // this.player.options.playbackRates = [];
        this.player.src({
          type: "application/x-mpegURL",
          src: liveUrl
        });
        if (true == isAutoPlay) {
          this.player.play();
        }
      }
    },
    /**
     * HLS
     * vodUrl 
     * siAutoPlay 
     */
    playVodWithHlsUrl(vodUrl, isAutoPlay = true) {
      if (this.player != null) {
        console.log("playVodWithHlsUrl:" + vodUrl);
        // this.player.reset();
        this.player.autoplay(isAutoPlay);
        // this.initOptions.playbackRates = [0.25, 0.5, 1.0, 1.5, 2.0];
        this.player.src({
          type: "application/x-mpegURL",
          src: vodUrl
        });
        if (true == isAutoPlay) {
          this.player.play();
        }
      }
    },
    /**
     * propsposterUrl
     */
    setPlayerPoster(imageUrl) {
      this.initOptions.poster = imageUrl;
      if (this.player != null) {
        // this.player.posterImage.setSrc(imageUrl);
        this.player.poster(imageUrl);
      }
    },
    /**
     * 
     */
