      // );
      var self = this;
      console.log("setup_sumaPalyer" + JSON.stringify(this.initOptions));
      this.player = this.$video(this.playerId, this.initOptions, function() {
        //https://html.spec.whatwg.org/multipage/media.html#mediaevents
        this.on("loadstart", () => {
          console.log("loadstart");
        });
        // 
        // this.on("progress", () => {
        //   console.log("progress");
        // });
        this.on("suspend", () => {
          console.log("suspend");
        });
        this.on("abort", () => {
          console.log("abort");
        });
        this.on("emptied", () => {
          console.log("emptied");
        });
        this.on("stalled", () => {
          console.log("stalled");
        });
        //
        this.on("loadedmetadata", () => {
          console.log("loadedmetadata");
          self.$emit("suma-player-loadedmetadata", self);
        });
        this.on("loadeddata", () => {
          console.log("loadeddata");
        });
        this.on("canplay", () => {
          console.log("canplay");
        });
        this.on("canplaythrough", () => {
          console.log("canplaythrough");
        });
        this.on("playing", () => {
          console.log("playing");
        });
        this.on("waiting", () => {
          console.log("waiting");
        });
        this.on("seeking", () => {
          console.log("seeking");
        });
        this.on("seeked", () => {
          console.log("seeked");
        });
        this.on("ended", () => {
          console.log("ended");
          self.$emit('plyerEnd')
        });
        this.on("durationchange", () => {
          console.log("durationchange");
        });
        // 
        // this.on("timeupdate", () => {
        //   console.log("timeupdate");
        // });
        this.on("pause", () => {
          console.log("pause");
        });
        this.on("ratechange", () => {
          console.log("ratechange");
        });
        this.on("resize", () => {
          console.log("resize");
        });
        this.on("loadstart", () => {
          console.log("loadstart");
        });
        this.on("loadstart", () => {
          console.log("loadstart");
        });
        this.on("loadstart", () => {
          console.log("loadstart");
        });
        this.on("ready", () => {
          // , 
          // this.volume(vm.video.volume);
          console.log("player_ready");
          // this.play(); //test
          self.$emit("suma-player-ready", self);
        });
        this.on("play", () => {
          // , 
          // this.volume(vm.video.volume);
          console.log("player_play");
          self.$emit("suma-player-play", self);
        });

        this.on("volumechange", () => {
          // 
          // vm.video.volume = this.volume();
          // window.localStorage.volume = this.volume();
        });

        this.on("error", () => {
          //  
          let err = this.error();
          if (err.code === 2) {
            // 
            let buffTime = this.bufferedEnd() - 6;
            this.load(vm.src);
            this.currentTime(buffTime);
            this.play();
          } else {
            // 
            // vm.alert.err(err.message);
            // this.dispose();
            // this.reset();
            // this.src({});
          }
          console.log("sumapalyer_error:" + JSON.stringify(err));
        });
      });
    },
    /**
     * 
     */
    resetPlayer() {
      if (this.player != null) {
        console.log("player_reset");
        //dispose()<video>videoNode
        this.player.dispose();
        // var videoNode =
        //   "<video id='" +
        //   this.playerId +
        //   "' ref='video' controls class='video-js vjs-default-skin vjs-big-play-centered' x5-playsinline='true' playsinline='true' webkit-playsinline ></video>";
        var videoNode = `<video
          id="${this.playerId}"
          ref="video"
          controls
          class="video-js vjs-default-skin vjs-big-play-centered"
          x5-playsinline="true"
          playsinline="true"
          webkit-playsinline >
          </video>`;
        document.getElementById("suma-player-parent").innerHTML += videoNode;
        this.setupPlayer();
        //TODO:iOS
      }
    },
    //
    pause() {
      if (this.player != null) {
        this.player.pause();
        console.log('player',this.player)
      }
    }
  },
  props: {
    // options: {
    //   type: Object,
    //   required: true
    // }
    posterUrl: {
      type: String,
      default: ""
    }
  },
  watch: {
    // src() {
    //   this.player.src(this.src);
    // }
    posterUrl(newV, oldV) {
      console.log("posterUrl_new:" + newV + " old:" + oldV);
      this.setPlayerPoster(newV);
    }
  },
  computed: {
    // src() {
    //   return this.options.sources[0].src;
    // },
    // video() {
    //   return this.$store.state.video;
    // }
  },
  mounted() {
    console.log("player_startMounted");
    const vm = this;
    document.addEventListener(
      "WeixinJSBridgeReady",
      function() {
        vm.player.play();
      },
      false
    );

    this.setupPlayer();

    console.log("player_mounted");
    //------------------div
     var w = window.innerWidth;
     this.height = w/16*9

  },
  beforeDestroy() {
    //
    this.player.dispose();
  }
};
</script>

