                    <Option v-for="item in videoList" :value="item.metaDataName" :key="item.metaDataValue">{{ item.metaDataName }}</Option>
                </Select>
            </FormItem>
             <FormItem label="" v-show="channelData.channelType==2">
                 <Select  style="width:350px"  v-model="audioType" :disabled="isGray" id="audioTypeId">
                    <Option v-for="item in audioList" :value="item.metaDataName" :key="item.metaDataValue">{{ item.metaDataName }}</Option>
                </Select>
            </FormItem>
        </Form>
      </div>
      <div style="display:flex;justify-content:center;align-items:center;margin-top:140px;margin-bottom:10px">
        <Button type="primary" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);margin-right:50px" @click="fileModalCancel"></Button>
         <Button type="primary" :disabled="isGray" @click="exportFile"></Button>
      </div>
    </Modal>
     <div>
        <player ref="player"  @fullArea="fullAreaEv" @cancelFullArea="cancelFullAreaEv" @isRecCreate="videoRecCreateSuccess" @isRecEnd="playEnding" @dottedSuccess="dottedSuccess"  @changeRecode="changeRecode"/> 
     </div>
     
  </div>
</template>
<script>
  import SumaPlayerMain from "@/components/SumaPlayerMain.vue";
  import SumaPlayerImportMain from "@/components/SumaPlayerImportMain.vue";
  import SumaPlayerImportDetail from "@/components/SumaPlayerImportDetail.vue"
  import SumaPlayerExportMain from '@/components/SumaPlayerExportMain.vue'
  import SumaPlayerExportDetail from '@/components/SumaPlayerExportDetail.vue'
  import videoImage from "../../../images/login_bg.jpg"
  import api from '@/api/api'
  import axios from 'axios'
  import Cookies from 'js-cookie'
  import player from'./player.vue'
  export default {
    components:{
      SumaPlayerMain,
      SumaPlayerImportMain,
      SumaPlayerImportDetail,
      SumaPlayerExportMain,
      SumaPlayerExportDetail,
      player
    },
     data(){
       return {
        isStart:1,
        recodePlanConfirmModal:false,
        innerHeight:0,
        returnBtnLoadingStatus:false,
        canvas:'2*2',
        selectPlanArr:[],
        selectHandArr:[],
        videoModal:false,
        listType:0,
        audioType:'',
        videoType:'',
        exportFileName:'',
        allChannelData:[],//
        channelData:{},
        videoImage,
        dbData:{},
        classNameInput:'',
        classModal:false,
        title:'',
        single:true,
        button1:'',
        flag:'import',
        importDetail:false,
        exportDetail:true,
        showDetail:false,
        treeData: [],
        timeArr:[
          {value1:['', ''],name1:''}
        ],
        timeArr1:[{value1:['', '']}],
        radioPlanTimeList:[
         
        ],
        radioHandTimeList:[],
         videoList:[],
         audioList:[],
        exportType:1,
        //======================
        pramName:'',
        today:'',
        importDate:'',
        importShowDate:'',
        exportDate:'',
        searchDate1:'',
        searchData:'',
        playIndex:0,
        playList:[],
        isGray:false,
        transfer:true,
        /**video */
        baseVideo:{
          playerIndex:'',
          groupIndex:''
        },
        recVideo:{
          playerIndex:'',
          groupIndex:''
        },
        /** */
        viewFlag:false,//
        timer:null,
        fileModal:false,
        exportListButton:'',
        exportTimeButton:'',
        /*-----------------------*/
        importListButton:'',
        widthBase:1,
        createMeidaNum:2,
        isbrower:false,
        moveToOther:false
       }
     },
     created(){
      /**v6.0.202007 */
      this.createMeidaNum = JSON.parse(sessionStorage.getItem('publicData')).createMeidaNum
      console.log('publicData',this.createMeidaNum)
      this.$bus.$on('showRecodeMode',()=>{
        this.recodePlanConfirmModal = true
      })
      this.$bus.$on('fullArea',(obj)=>{
        this.fullAreaEv(obj)
      })
      this.$bus.$on('cancelFullArea',(obj)=>{
        this.cancelFullAreaEv(obj)
      })
      this.$bus.$on('isRecCreate',(obj)=>{
        this.videoRecCreateSuccess(obj)
      })
      this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true; //
      //
      this.selectCategoryChannelList()
      //
      this.searchRecordChannelList()
      let selectAllData = JSON.parse(localStorage.getItem('selectAllData'))
      //
      if(selectAllData.length){
        this.videoList = selectAllData[4].metaDictList
        this.videoType = this.videoList[0].metaDataName
        this.audioList = selectAllData[5].metaDictList
        this.audioType = this.audioList[0].metaDataName
      }
      this.checkServerStatus()
      this.timer = setInterval(()=>{
        this.checkServerStatus()
      },300000)
      this.$nextTick(()=>{
        this.updateExportMenu()
      })
     },
     mounted(){
      let vm = this
      /** dom*/
      this.importDomChange()
      setTimeout(() => {
        if(this.$sumaQtPlayer){
          this.$refs.player.destroyQmlPlayer([{groupIndex:-2,playerIndex:-2}])
          this.createPlayer()
          this.isbrower = false
        }else{
          let vm = this
          this.isbrower = true
          /**/
          document.getElementById("suma-player").addEventListener('fullscreenchange', vm.onFullScreen)
          document.getElementById("suma-player1").addEventListener('fullscreenchange', vm.importFullScreen)
          document.getElementById("suma-player2").addEventListener('fullscreenchange', vm.importFullScreen)
          document.getElementById("suma-player3").addEventListener('fullscreenchange', vm.importFullScreen)
          document.getElementById("suma-player4").addEventListener('fullscreenchange', vm.importFullScreen)
        }
      }, 300);
      /** */
        window.onresize = function(){
          /**dom */
          vm.importDomChange()
          /**dom */
          vm.updateExportMenu()
          //
          vm.playerAdjust()
        }
     },
     beforeDestroy(){
        let param = [{
          groupIndex:-2,
          playerIndex:-2,
        }]
        if(this.$sumaQtPlayer){
          this.$refs.player.destroyQmlPlayer(param)
        }
        this.$bus.$off('showRecodeMode')
        this.$bus.$off('fullArea')
        this.$bus.$off('cancelFullArea')
        this.$bus.$off('isRecCreate')
     },
     methods:{
      allowDrop(e){
        e.preventDefault()
        this.moveToOther = true
      },
      pause(type){
        if(type == 'videoMain'){
          this.$refs.videoImportMain.pause()
          this.$refs.videoImportDetail.pause()
          this.$refs.videoExportMain.pause()
          this.$refs.videoExportDetail.pause()
        }else if(type == 'videoImportDetail'){
          this.$refs.videoMain.pause()
          this.$refs.videoImportMain.pause()
          this.$refs.videoExportMain.pause()
          this.$refs.videoExportDetail.pause()
        }else if(type == 'videoImportMain'){
          this.$refs.videoMain.pause()
          this.$refs.videoImportDetail.pause()
          this.$refs.videoExportMain.pause()
          this.$refs.videoExportDetail.pause()
        }else if(type == 'videoExportMain'){
          this.$refs.videoMain.pause()
          this.$refs.videoImportMain.pause()
          this.$refs.videoImportDetail.pause()
          this.$refs.videoExportDetail.pause()
        }else if(type == 'videoExportDetail'){
          this.$refs.videoMain.pause()
          this.$refs.videoImportMain.pause()
          this.$refs.videoImportDetail.pause()
          this.$refs.videoExportMain.pause()
        }
      },
      /** */
      exitFullscreen() {
        var de = document;
        if (de.exitFullscreen) {
            de.exitFullscreen();
        } else if (de.mozCancelFullScreen) {
            de.mozCancelFullScreen();
        } else if (de.webkitCancelFullScreen) {
            de.webkitCancelFullScreen();
        }
      },
      /**video */
      importFullScreen(){
        var isFullscreenNow = document.webkitFullscreenElement !== null
        if(!isFullscreenNow){
          /***/
          this.showDetail = false
          this.$refs.videoMain.playVodWithHlsUrl(this.channelData.playUrl)
          this.$refs.videoMain.showChannelName(this.channelData.channelName)
          this.pause('videoMain')
        }
      },
       /**video */
       onFullScreen(ev){
         console.log('',ev)
         let vm = this
         var isFullscreenNow = document.webkitFullscreenElement !== null
         if(isFullscreenNow && this.channelData.channelId){
          this.showDetail = true
          /** */
          this.exitFullscreen()
          for(let item of this.allChannelData){
            if(item.channelId === this.channelData.channelId){
              this.channelData.channelType = item.channelType
              this.channelData.playUrl = item.playUrl
              this.channelData.channelName = item.channelName
              break
            }
          }
            //
          let year = new Date().getFullYear()>9?new Date().getFullYear():'0'+new Date().getFullYear()
          let month = new Date().getMonth()+1>9?new Date().getMonth()+1:'0'+(new Date().getMonth()+1)
          let  day = new Date().getDate()>9?new Date().getDate():'0'+new Date().getDate()
          this.today = year+'-'+month+'-'+day
          this.importDate = this.today
          this.importShowDate = this.today
          this.exportDate = this.today
          this.searchData = this.today//
          //
          this.searchRecordProgramList()
          //
          this.searchPlanListToImport()
          this.$nextTick(()=>{
              console.log('flag',this.flag)
              console.log('importDetail',this.importDetail)
              if(this.flag == 'import'){
                if(this.importDetail){
                  this.$refs.videoImportDetail.playVodWithHlsUrl(this.channelData.playUrl)
                  this.$refs.videoImportDetail.showChannelName(this.channelData.channelName)
                  this.pause('videoImportDetail')
                }else{
                  this.$refs.videoImportMain.playVodWithHlsUrl(this.channelData.playUrl)
                  this.$refs.videoImportMain.showChannelName(this.channelData.channelName)
                  this.pause('videoImportMain')
                }
              }else{
                if(this.exportDetail){
                  this.$refs.videoExportDetail.playVodWithHlsUrl(this.channelData.playUrl)
                  this.$refs.videoExportDetail.showChannelName(this.channelData.channelName)
                  this.pause('videoExportDetail')
                }else{
                  this.$refs.videoExportMain.playVodWithHlsUrl(this.channelData.playUrl)
                  this.$refs.videoExportMain.showChannelName(this.channelData.channelName)
                  this.pause('videoExportMain')
                }
              }
          })
         }
       },
       showRecodeMode(){
        this.recodePlanConfirmModal = true
       },
       recodeConfirm(){
         this.$refs.player.recodeConfirm()
       },
       recodeCancel(){
         this.recodePlanConfirmModal = false
         this.$refs.player.recodeCancel()
       },
       recodeSwitch(val){
         this.$refs.player.isStart = val
       },
       playerAdjust(){
         if(!this.showDetail){
            setTimeout(() => {
              let odiv = document.getElementById('showVideoPlayer')
              let allWidth = odiv.offsetWidth
              let allHeight = odiv.offsetHeight
              let x = odiv.getBoundingClientRect().left
              let y = odiv.getBoundingClientRect().top
              let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
              let rows = resolution? resolution.split("*")[0]/1:2
              let columns = resolution? resolution.split("*")[1]/1:2
              let groupIndex = this.recVideo.groupIndex
              let param = {
                groupIndex,x,y,width:allWidth,height:allHeight,rows,columns
              }
              if(this.$sumaQtPlayer){
                this.$refs.player.adjustQmlPlayer(param)
              }
             }, 300);
           }else{
             setTimeout(() => {
               let odiv
              if(this.flag == 'import'){
                if(this.importDetail){
                   odiv = document.getElementById('fullArea')
                }else{
                   odiv = document.getElementById('importPlayer')
                }
              }else{
                if(this.exportDetail){
                  odiv = document.getElementById('fullArea1')
                }else{
                  odiv = document.getElementById('exportPlayer')
                }
              }
              let width = odiv.offsetWidth
              let height = odiv.offsetHeight
              let x = odiv.getBoundingClientRect().left
              let y = odiv.getBoundingClientRect().top
              let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
              let rows = resolution? resolution.split("*")[0]/1:2
              let columns = resolution? resolution.split("*")[1]/1:2
              let groupIndex = this.recVideo.groupIndex
              let param = {
                x,y,width,height,rows,columns,groupIndex
              }
               if(this.$sumaQtPlayer){
                this.$refs.player.adjustQmlPlayer(param)
              }
              //
               let param1 = [{
                 playerIndex:this.recVideo.playerIndex,
                 groupIndex,
                 channelId:this.channelData.channelId,
                 business:{},
                 player:{
                   isFullArea:true,doAction:'play'
                 }
               }]
               if(this.$sumaQtPlayer){
                 this.$refs.player.setQmlPlayerProperty(param1)
              }
             }, 300);
           }
       },
       checkServerStatus(){
         api.checkServerStatus({}).then(res=>{
           console.log('')  
        })
       },
       //
       fullAreaEv(ev){
          this.canvas = ""
          this.showDetail = true
          this.channelData = {
            channelId:ev.channelId
          }
          for(let item of this.allChannelData){
            if(item.channelId === ev.channelId){
              this.channelData.channelType = item.channelType
              this.channelData.playUrl = item.playUrl
              this.channelData.channelName = item.channelName
              break
            }
          }
            //
          let year = new Date().getFullYear()>9?new Date().getFullYear():'0'+new Date().getFullYear()
          let month = new Date().getMonth()+1>9?new Date().getMonth()+1:'0'+(new Date().getMonth()+1)
          let  day = new Date().getDate()>9?new Date().getDate():'0'+new Date().getDate()
          this.today = year+'-'+month+'-'+day
          this.importDate = this.today
          this.importShowDate = this.today
          this.exportDate = this.today
          this.searchData = this.today//
          //
          this.searchRecordProgramList()
          //
          this.searchPlanListToImport()
          this.$nextTick(()=>{
              let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
              let rows = resolution? resolution.split("*")[0]/1:2
              let columns = resolution? resolution.split("*")[1]/1:2
              let odiv
              if(this.flag == 'import'){
                if(this.importDetail){
                   odiv = document.getElementById('fullArea')
                }else{
                   odiv = document.getElementById('importPlayer')
                }
              }else{
                if(this.exportDetail){
                  odiv = document.getElementById('fullArea1')
                }else{
                  odiv = document.getElementById('exportPlayer')
                }
              }
              let width = odiv.offsetWidth
              let height = odiv.offsetHeight
              let x = odiv.getBoundingClientRect().left
              let y = odiv.getBoundingClientRect().top
              let groupIndex = ev.groupIndex
              let obj = {
                x,y,width,height,rows,columns,groupIndex
              }
              this.$refs.player.adjustQmlPlayer(obj)
              //
              let param = [{
                playerIndex:ev.playerIndex,
                groupIndex,
                channelId:ev.channelId,
                business:{},
                player:{
                  isFullArea:true,doAction:'play',mode:'rec'
                }
              }]
              this.$refs.player.setQmlPlayerProperty(param)
          })
          this.recVideo.playerIndex = ev.playerIndex
          this.recVideo.groupIndex = ev.groupIndex
       },
       //
       cancelFullAreaEv(ev){
         this.canvas = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):'2*2'
          this.showDetail = false
          this.$nextTick(()=>{
            let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
            let rows = resolution? resolution.split("*")[0]/1:2
            let columns = resolution? resolution.split("*")[1]/1:2
            let odiv = document.getElementById('showVideoPlayer')
            let width = odiv.offsetWidth
            let height = odiv.offsetHeight
            let x = odiv.getBoundingClientRect().left
            let y = odiv.getBoundingClientRect().top
            let groupIndex = ev.groupIndex
            let obj = {
              x,y,width,height,rows,columns,groupIndex
            }
            this.$refs.player.adjustQmlPlayer(obj)

            /**================*/
            if(this.viewFlag){
              let arr = JSON.parse(sessionStorage.getItem('initPlay'))
              let playUrl = ''
              let channelName = ''
              for(let item of arr){
                if(item.playerIndex === ev.playerIndex){
                    playUrl = item.playerInfo.channelUrl
                    channelName = item.playerInfo.channelName
                    break
                }
              }
              let param = [
                  {
                    "x": 200,
                    "y": 100,
                      groupIndex:this.recVideo.groupIndex,
                      playerIndex:this.recVideo.playerIndex,   
                      playerInfo: {
                        channelId: this.channelData.channelId,
                        channelName: channelName,
                        channelUrl:playUrl,
                      }
                  }
              ]
              this.$refs.player.prepareQmlPlayer2(param)
              let stateArr = JSON.parse(sessionStorage.getItem('monitorState'))
              this.$refs.player.setQmlPlayerProperty(stateArr)
              this.viewFlag = false
            }
          })
       },
       renderContent(h, { node, data, store }) {
          if(data.categoryId){
              return (
                <div class="custom-tree-node" style="width:100%;display:flex;justify-content:space-between;align-items:center">
                   <Tooltip placement="top-start" transfer={this.transfer} delay={1500} offset={50}>
                    <p style={{width:100*this.widthBase+'px',overflow: 'hidden','white-space': 'nowrap','text-overflow': 'ellipsis','font-size':'16px','color':'rgba(255,255,255,1)',marginTop:'5px',fontSize:'14px'}} on-dblclick={()=>this.clkDb(data)}>{node.label}</p>
                    <div slot="content">
                        <p style="white-space: normal;fontSize:14px;margin-top:30px">
                          {node.label}
                        </p>
                    </div>
                  </Tooltip>
                  <span>
                    <i class="el-icon-delete" on-click={ () => this.removeCategory(node, data) }></i>
                  </span>
                </div>
                );
           }else{
              return (
                <div class="custom-tree-node">
                  <Tooltip placement="top-start" transfer={this.transfer} offset={50}>
                    <p style={{width:100*this.widthBase+'px',overflow: 'hidden','white-space': 'nowrap','text-overflow': 'ellipsis','font-size':'16px','color':'rgba(255,255,255,1)',marginTop:'5px',fontSize:'14px'}}>{node.label}</p>
                    <div slot="content">
                        <p style="white-space: normal;fontSize:14px">
                          {node.label}
                        </p>
                    </div>
                  </Tooltip>
                </div>
              );
           }
      },
        removeCategory(node,data){
          if (this.isGray == true) {
            return false;
          }
          console.log('node',node)
          console.log('data',data)
          let param = {
            categoryId:data.categoryId
          }
          api.deleteCategory(param).then(res=>{
            this.$Message.success('')
            this.selectCategoryChannelList()
          })
        },
       //
       allShut(){
        //
        sessionStorage.setItem('initPlay','')
        //
        sessionStorage.setItem('monitorState','')
        //
          //
          this.showDetail = false
          //
           this.$nextTick(()=>{
            let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
            let rows = resolution? resolution.split("*")[0]/1:2
            let columns = resolution? resolution.split("*")[1]/1:2
            let odiv = document.getElementById('showVideoPlayer')
            let width = odiv.offsetWidth
            let height = odiv.offsetHeight
            let x = odiv.getBoundingClientRect().left
            let y = odiv.getBoundingClientRect().top
            let groupIndex = this.recVideo.groupIndex
            let param = [
              {
                groupIndex:groupIndex,
                playerIndex: -2,
                channelId: "12",
                business: {
                  isRecording: false,//
                  isDotting: false//
                },
                player: {
                  isFullArea: false,
                  doAction: "stop"
                }
              }
            ]
            this.$refs.player.setQmlPlayerProperty(param)
             //===================
            let obj = {
              x,y,width,height,rows,columns,groupIndex
            }
            this.$refs.player.adjustQmlPlayer(obj)
          })
       },
       createPlayer(){
          let resolution = sessionStorage.getItem('resolution')?sessionStorage.getItem('resolution'):''
          this.canvas = resolution?resolution:'2*2'
          let rows = resolution? resolution.split("*")[0]/1:2
          let columns = resolution? resolution.split("*")[1]/1:2
          let odiv = document.getElementById('showVideoPlayer')
          let allWidth = odiv.offsetWidth
          let allHeight = odiv.offsetHeight
          let x = odiv.getBoundingClientRect().left
          let y = odiv.getBoundingClientRect().top
          console.log('dom',document.getElementById('showVideoPlayer'))
          this.$refs.player.createQmlPlayer(x,y,allWidth,allHeight,rows,columns,'rec')
       },
       //========
       handleUpload(file){
         console.log('',file);
         let FileExt = file.name.replace(/.+\./, "");       
          if (['txt'].indexOf(FileExt.toLowerCase()) === -1){            
            this.$Message.error('txt');            
            return false;
          }  
         this.upload(file);
         return false;
       },
       upload(file){
           var jsonText = {
            'versionName': '4.1.8',
            'versionCode': '340',
            'operatorId': Cookies.get('operatorId'),
            'operatorGroup': Cookies.get('operatorGroup'),
            info: {
              channel_id:this.channelData.channelId
            }
          }
          var param = new FormData()
          param.append('jsonText', JSON.stringify(jsonText))
          param.append('file', file)
          let token = Cookies.get('token')
          var instance = axios.create({
            baseURL: './api/drbs',
            timeout: 5000,
            headers: {'Content-type': 'multipart/form-data', 'accessToken': `${token}`}
          });
          // console.log(param)
          instance.post('/cmc/channel/channelProgramUpload', param).then((res) => {
            console.log("================", res.data.errorMessage);
            if (res.data.status == 0 ) {
              this.$Message.success('');
              this.searchPlanListToImport();
            } else {
              this.$Message.error(res.data.errorMessage);
            }
          })
       },
       /**/dom */
       importDomChange(){
         this.innerHeight = window.innerHeight
         let width = window.innerWidth
         let height = window.innerHeight
         this.widthBase = (width-160)/(1024-160)
         let impor_card_left = document.getElementById('chnnel_mon_left')
         impor_card_left.style.height = height-160 +'px'
         /** */
         let elTree = document.getElementById('elTree')
         elTree.style.height = height-250 + 'px'
         /**------ */
         let importList = document.getElementById('importList')
         importList.style.height = height - 360 + 'px'
         /**() */
         let importPlayer = document.getElementById('importPlayer')
         importPlayer.style.height = height-290 + 'px'
       },
       /*dom*/
       updateExportMenu(){
        /** */
         let height = window.innerHeight;
         let offset=height-768;
         let listMenu=document.getElementById('exportListMenu');
         let listMenuPlan=document.getElementById('exportListPlanMenu');
         let listMenuHand=document.getElementById('exportListHandMenu');
         let timeMenu = document.getElementById('exportTimeMenu');
         listMenu.style.height=442+offset+"px";
         listMenuPlan.style.height=300+offset+"px";
         listMenuHand.style.height=300+offset+"px";
         timeMenu.style.height=400+offset+"px";
         /** */
         let exportPlayer = document.getElementById('exportPlayer')
         exportPlayer.style.height = height-290 + 'px'
       },
      //=====================
       exportFileList(){
        //  alert(1112)
        //  console.log("", this.importShowDate);
         if (!this.importShowDate) {
           this.$Message.info('');
           return false;
         }
          window.location.href=`/api/drbs/cmc/channel/exportChannelProgramFile?channelId=${this.channelData.channelId}&date=${this.importShowDate}`
       },
       //
       searchRecordChannelList(){
         let param ={
           channelName:'',
           userId:JSON.parse(localStorage['loginRes']).user.userId,
           pageNo:-1
         }
         api.searchRecordChannelList(param).then(res=>{
           this.allChannelData = res.result
           console.log('',res)
         })
       },
       //
       showChannel(resolution){
        //
        this.showDetail = false
        // this.$nextTick(()=>{
        //   this.$refs.player.destroyQmlPlayer([{groupIndex:-2,playerIndex:-2}])
        //   sessionStorage.setItem('resolution',resolution)
        //   this.createPlayer()
        // })
        this.$nextTick(()=>{
          //
          if(!this.$sumaQtPlayer){
            this.$refs.videoMain.playVodWithHlsUrl(this.channelData.playUrl)
            this.$refs.videoMain.showChannelName(this.channelData.channelName)
            return false
          }
          let rows = resolution.split("*")[0]/1
          let columns = resolution.split("*")[1]/1
          let odiv = document.getElementById('showVideoPlayer')
          let width = odiv.offsetWidth
          let height = odiv.offsetHeight
          let x = odiv.getBoundingClientRect().left
          let y = odiv.getBoundingClientRect().top
          let groupIndex = this.recVideo.groupIndex
          let obj = {
            x,y,width,height,rows,columns,groupIndex,mode:'rec'
          }
          this.$refs.player.adjustQmlPlayer(obj)
           //
          let recordPlayUrlChange = sessionStorage.getItem('initPlay')?JSON.parse(sessionStorage.getItem('initPlay')):[]
          /** */
          let arr = JSON.parse(sessionStorage.getItem('monitorState'))
          let playInitArr = []
          /** */
          let recodeInitArr = []
          if(sessionStorage.getItem('resolution') ==="2*2"){
            if(resolution ==="3*3"||resolution == '4*4'){
              for(let item1 of recordPlayUrlChange){
                if(item1.playerIndex>3){
                  playInitArr.push(item1)
                }
              }
              for(let item2 of arr){
                if(item2.playerIndex>3){
                  recodeInitArr.push(item2)
                }
              }
              //
             this.$refs.player.prepareQmlPlayer2(playInitArr)
             //
             this.$refs.player.setQmlPlayerProperty(recodeInitArr)
            }
          }else if(sessionStorage.getItem('resolution') ==="3*3"){
              if(resolution == '4*4'){
                for(let item1 of recordPlayUrlChange){
                  if(item1.playerIndex>8){
                    playInitArr.push(item1)
                  }
                }
                for(let item2 of arr){
                  if(item2.playerIndex>8){
                    recodeInitArr.push(item2)
                  }
                }
                //
                this.$refs.player.prepareQmlPlayer2(playInitArr)
                //
                this.$refs.player.setQmlPlayerProperty(recodeInitArr)
              }
            }
           sessionStorage.setItem('resolution',resolution)
        })
