       //
       paragramView(){
        this.viewFlag = true
        let publicPlayUrl = this.channelData.playUrl
        this.playList = []
        let now = new Date().getTime()
        this.playIndex = 0
        if(this.exportType==1){
          if(this.listType==0){
            if(!this.radioPlanTimeList.length){
              this.$Message.info('')
              return false
            }
            if(!this.selectPlanArr.length){
              this.$Message.info('')
              return false
            }
            for(let id of this.selectPlanArr){
              for(let item of this.radioPlanTimeList){
                if(id === item.id){
                  let playUrl = publicPlayUrl+'?starttime='+new Date(item.startDate).getTime()/1000+'&endtime='+new Date(item.endDate).getTime()/1000
                  this.playList.push(playUrl)
                  break
                }
              }
            }
          }else{
            if(!this.radioHandTimeList.length){
              this.$Message.info('')
              return false
            }
            if(!this.selectHandArr.length){
              this.$Message.info('')
              return false
            }
            for(let id of this.selectHandArr){
              for(let item of this.radioHandTimeList){
                if(id === item.id){
                  let playUrl = publicPlayUrl+'?starttime='+new Date(item.startDate).getTime()/1000+'&endtime='+new Date(item.endDate).getTime()/1000
                  this.playList.push(playUrl)
                }
              }
            }
          }
           if(this.$sumaQtPlayer){
              this.playInit()
            }else{
               /** */
              this.videoInitPlay()
            }
        }else{
          //=================================
          let selectflag = true //
          let isCanHandleTime = true  //
          let startEndSame = false //
          let now = new Date().getTime()
          for(let item of this.timeArr1){
            if(!item.value1[0]||!item.value1[1]){
              selectflag = false
            }
            if(item.value1[0] === item.value1[1]){
              startEndSame = true
            }
            let end_date = this.searchData +' '+ item.value1[1]
            if(new Date(end_date).getTime() > now){
              isCanHandleTime = false
            }
          }
          if(!selectflag){
            this.$Message.info('')
            return false
          }
          if(startEndSame){
            this.$Message.info('')
            return false
          }
          if(!isCanHandleTime){
             this.$Message.info('')
            return false
          }
          for(let item of this.timeArr1){
            let playUrl = publicPlayUrl + "?starttime="+new Date(this.searchData+' '+item.value1[0]).getTime()/1000+"&endtime="+new Date(this.searchData+' '+item.value1[1]).getTime()/1000
            this.playList.push(playUrl)
          }
          if(this.$sumaQtPlayer){
            //qt
            this.playInit()
          }else{
            /** */
            this.videoInitPlay()
          }
        }
       },
       /** */
       videoRecCreateSuccess(obj){
         this.recVideo.groupIndex = obj.groupIndex
       },
       //
       playEnding(obj){
         /** */
         if(this.viewFlag){
            /** */
          if(obj.playerIndex === this.recVideo.playerIndex){
              if(this.playIndex === this.playList.length-1){
                /**================*/
                let arr = JSON.parse(sessionStorage.getItem('initPlay'))
                let playUrl = ''
                let channelName = ''
                for(let item of arr){
                  if(item.playerIndex === obj.playerIndex){
                     playUrl = item.playerInfo.channelUrl
                     channelName = item.playerInfo.channelName
                     break
                  }
                }
                let param = [
                    {
                      "x": 200,
                      "y": 100,
                        groupIndex:this.recVideo.groupIndex,
                        playerIndex:this.recVideo.playerIndex,   
                        playerInfo: {
                          channelId: this.channelData.channelId,
                          channelName: channelName,
                          channelUrl:playUrl,
                        }
                    }
                ]
                this.$refs.player.prepareQmlPlayer2(param)
                let stateArr = JSON.parse(sessionStorage.getItem('monitorState'))
                this.$refs.player.setQmlPlayerProperty(stateArr)
                return false
              }
              this.playIndex++
              this.playInit()
          }
         }
       },
