        ///
        createChannelProgramList(){
          let programs = []
          let nameFlag = true //name
          let nameTrimFlag = true //name
          let timeFlag = true //
          let timeIsSame = false //

          for (let i = 0; i < this.timeArr.length - 1; i++) {
            for (let j = i + 1; j < this.timeArr.length; j++) {
              if ((this.timeArr[i].value1[0] == this.timeArr[j].value1[0]) && (this.timeArr[i].value1[1] == this.timeArr[j].value1[1])) {
                // alert(1234567);
                this.$Message.info('');
                return false;
              }
            }
          }
          
          for(let item of this.timeArr){
            let obj = {};
            obj.name = item.name1;
            obj.startDate = this.importShowDate +' '+ item.value1[0];
            obj.endDate = this.importShowDate +' '+ item.value1[1];
            obj.type='1';

            if(!item.name1){
              nameFlag = false
            }else if(!(item.name1.trim())){
              nameTrimFlag = false
            }
            if(!item.value1[0]||!item.value1[1]){
              timeFlag = false
            }
            if(obj.startDate === obj.endDate&&obj.startDate){
              timeIsSame = true
            }
            programs.push(obj)
          }
          if(!nameFlag){
            this.$Message.info('')
            return false
          }
          if(!nameTrimFlag){
            this.$Message.info('')
            return false
          }
          if(!timeFlag){
            this.$Message.info('')
            return false
          }
          if(timeIsSame){
            this.$Message.info('')
            return false
          }
          let param = {
            channelId:this.channelData.channelId,
            programs
          }
          api.createChannelProgramList(param).then(res=>{
            this.$Message.success('')
          })
        },

