<template>
  <div id="monRoot">
     <!---->
     <!-- <Modal
        v-model="recodePlanConfirmModal"
        width="638"
        id="planModal"
        :closable="false"
        :mask-closable="false"
        :transfer="false"
       >
       <Form ref="formInline" style="margin-top:52px;padding:0 68px">
        <FormItem>
           <span style="fontSize:16px;font-weight:400;font-family:Microsoft YaHei;color:rgba(254,254,254,1)">?</span>
           <label style="margin-left:45px">
             <RadioGroup v-model="isStart" style="color:#fff">
                <Radio :label="1"></Radio>
                <Radio :label="0"></Radio>
            </RadioGroup>
          </label> 
         </FormItem>
         <FormItem style="marginTop:42px">
           <div style="color:rgba(175,188,204,1);font-weight:400;font-size:14px;font-family:Microsoft YaHei;">
             ""///
           </div>
         </FormItem>
          <FormItem>
           <div style="color:rgba(175,188,204,1);font-weight:400;font-size:14px;font-family:Microsoft YaHei;">
             ""
           </div>
         </FormItem>
      </Form>
       <p slot="footer" style="display:flex;justify-content:center;margin-top:38px;margin-bottom:20px">
           <Button @click="recodeCancel" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);"></Button>
           <Button type="primary" @click="recodeConfirm" :disabled="isGray" style="margin-left:49px"></Button>
       </p>
    </Modal> -->

     <!---->
     <Modal
        v-model="dottingModal"
        width="600"
        :closable="false"
        :mask-closable="false"
       >
       <Form ref="formInline" :label-width="120">
        <FormItem label="">
           <Input style="width:200px" size="small" :maxlength="20 " v-model="dottingName"></Input>
           <span style="color:#fff">{{dottingName.length}}/20</span>
          <Button type="primary" size="small"  style="margin-left:30px" @click="dottingConfirm"></Button>
          <Button size="small" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);margin-left:10px" @click="dottingCancel"></Button>
         </FormItem>
      </Form>
       <p slot="footer"></p>
    </Modal>
  </div>
</template>
<script>
// import QWebChannel from '@/qwebchannel.js'
import api from '@/api/api'
export default {
  data(){
    return {
     dottingName:'',
     recodePlanConfirmModal:false,
     dottingModal:false,
     sumaQtPlayer:'',
     isStart:1,//
     recodeSwitchFlag:false,//
     receiveObj:{},//
     recordPlayUrlChange:[],//
     playerType:'',
     playerbaseIndex:0,//groupindex
     playerRecIndex:0,//groupIndex
     isGray:false
    }
  },
  created(){
    this.isGray = ((JSON.parse(localStorage.getItem('loginRes'))).user.operationPermissions) == 1 ? false:true;
    this.$bus.$on('showPlay',param=>{
      this.setQmlPlayerProperty(param)
    })
    this.$bus.$on('recodeSwitchFlag',val=>{
      this.recodeSwitchFlag = val
    })
    this.$bus.$on('receiveObj',val=>{
      this.receiveObj = val
    })
    this.$bus.$on('dottingModal',val=>{
      this.dottingModal = val
      this.dottingName = ''
    })
  },
  destroyed(){
    this.$bus.$off('showPlayer')
    this.$bus.$off('recodeSwitchFlag')
    this.$bus.$off('receiveObj')
    this.$bus.$off('dottingModal')

  },
  mounted(){
    // setTimeout(() => {
    //   // console.log(000,this.$sumaQtPlayer)
    //   console.log(555,this.$sumaQtPlayer);
    // })
    console.log(999,this.$sumaQtPlayer)
     setTimeout(() => {
       let vm = this
      let arr  = []
      if(vm.$sumaQtPlayer){
        //  vm.getServerTime()/**v6.0.202007 */
        //  vm.getVersion()
      }
     }, 0);
  },
  methods:{
    //
    getServerTime(){
      api.getServerTime({}).then(res=>{
      let secondTimeStamp = res.result.secondTimeStamp
       this.setQmlPlayerServerTime(secondTimeStamp)
      })
    },
    recodeConfirm(){
      if(this.recodeSwitchFlag){
        //========================
         let param = {
              isStartRecord:0,//0 1
              autoRecordPlan:this.isStart,// 0 1
              channelIdList:[this.receiveObj.channelId]
            }
            let arr = JSON.parse(sessionStorage.getItem('monitorState'))
            api.startOrStopRecord(param).then(res=>{
              this.$Message.success('')
              this.$emit('changeRecode')
              this.recodePlanConfirmModal = false
               /** */
              let param = [{
                groupIndex:-2,
                playerIndex:-2,
                channelId:'',
                business:{},
                player:{
                  isVisible:true
                }
              }]
              this.setQmlPlayerProperty(param)
              //
              for(let item of arr){
                if(item.playerIndex === this.receiveObj.playerIndex){
                  item.business.isRecording = true
                }else{
                  //
                  if(item.channelId === this.receiveObj.channelId){
                    item.business.isRecording = true
                  }
                }
              }
              this.setQmlPlayerProperty(arr)
              sessionStorage.setItem('monitorState',JSON.stringify(arr))
            },err=>{
              //
               this.setQmlPlayerProperty(arr)
            })
      }else{
        let param = {
              isStartRecord:1,//0 1
              autoRecordPlan:this.isStart,// 0 1
              channelIdList:[this.receiveObj.channelId]
            }
            let arr = JSON.parse(sessionStorage.getItem('monitorState'))
            api.startOrStopRecord(param).then(res=>{
              this.$Message.success('')
              this.$emit('changeRecode')
              this.recodePlanConfirmModal = false
               /** */
              let param = [{
                  groupIndex:-2,
                  playerIndex:-2,
                  channelId:'',
                  business:{},
                  player:{
                    isVisible:true
                  }
                }]
                this.setQmlPlayerProperty(param)
              //
              for(let item of arr){
                if(item.playerIndex === this.receiveObj.playerIndex){
                  item.business.isRecording = false
                }else{
                  if(item.channelId === this.receiveObj.channelId){
                    item.business.isRecording = false
                  }
                }
              }
              this.setQmlPlayerProperty(arr)
              sessionStorage.setItem('monitorState',JSON.stringify(arr))
            },err=>{
               this.setQmlPlayerProperty(arr)
               this.recodePlanConfirmModal = false
            })
      }
    },
    //===
    recodeCancel(){
      this.recodePlanConfirmModal = false
      let arr = JSON.parse(sessionStorage.getItem('monitorState'))
      this.setQmlPlayerProperty(arr)
      /** */
      let param = [{
          groupIndex:-2,
          playerIndex:-2,
          channelId:'',
          business:{},
          player:{
            isVisible:true
          }
        }]
        this.setQmlPlayerProperty(param)
    },
    /**  */
    dottingConfirm(){
      if(!this.dottingName){
        this.$Message.info('')
        return false
      }else if(!(this.dottingName.trim())){
        this.$Message.info('')
        return false
      }
      let  arr = JSON.parse(sessionStorage.getItem('monitorState'))
      let startTime = ''
      for(let item of arr){
        if(item.playerIndex === this.receiveObj.playerIndex){
          startTime = item.timestamp
          break
        }
      }
      //======================
      let param = {
        channelId:this.receiveObj.channelId,
        name:this.dottingName,
        startDate:this.timestampToTime(startTime),
        endDate:this.timestampToTime(this.receiveObj.timestamp),
        type:'2'
      }
      api.createChannelProgram(param).then(res=>{
        this.$Message.success('')
        this.$emit('dottedSuccess')
        //
          for(let item of arr){
            if(item.playerIndex === this.receiveObj.playerIndex){
              item.business.isDotting = false
            }else{
              if(item.channelId === this.receiveObj.channelId){
                item.business.isDotting = false
              }
            }
          }
          this.setQmlPlayerProperty(arr)
          sessionStorage.setItem('monitorState',JSON.stringify(arr))
          this.dottingModal = false
      },err=>{
        //
        this.setQmlPlayerProperty(arr)
      })
    },
    //
    dottingCancel(){
      let  arr = JSON.parse(sessionStorage.getItem('monitorState'))
      this.dottingModal = false
      this.setQmlPlayerProperty(arr)
    },
    //unit
    timestampToTime(timestamp){
      let  date = new Date(timestamp * 1000)
      let year = date.getFullYear()
      let month = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1)
      let day = date.getDate()
      let hour = date.getHours()
      let min = date.getMinutes()
      let s = date.getSeconds()
      return year+"-"+month+'-'+day + ' '+hour+":"+min+":"+s
    },
    //
    setQmlPlayerServerTime(serverUnixTimestamp) {
        var jsonDemo = {
          serverUnixTimestamp,
        };
        this.$sumaQtPlayer.setServerTime(JSON.stringify(jsonDemo), function (
          callback
        ) {
         
        });
    },
    //
    createQmlPlayer(x,y,witdh,height,rows,columns,mode){
      let vm = this
       //
      vm.playerType = 'rec'
      sessionStorage.setItem('playerType','rec')
      let param = {
        mode:mode,
        x: x,
        y: y,
        width: witdh,
        height: height,
        rows: rows,
        columns: columns,
      };
      this.$sumaQtPlayer.createPlayer(JSON.stringify(param), function (
        callback
      ) {
        let res = JSON.parse(callback)
          if(res.status === 'OK'){
           
            //
            //1.
            vm.prepareQmlPlayer1()
            //2
              let arr = JSON.parse(sessionStorage.getItem('monitorState'))
              if(arr){
                vm.setQmlPlayerProperty(arr)
              }
          }
      })
    },
    //
    prepareQmlPlayer(x,y,channelName,channelId,channelUrl,recodeFlag,timeshiftTotalInSec){
      let vm = this
        let param = [
          {
            x: x,
            y: y,
            groupIndex:-1,
            playerIndex: -1, //playerIndex-1playerIndex
            playerInfo: {
              channelId: channelId,
              channelName:channelName,
              channelUrl: channelUrl,
              timeshiftTotalInSec:timeshiftTotalInSec
            },
          },
        ];
        this.recordPlayUrlChange = sessionStorage.getItem('initPlay')?JSON.parse(sessionStorage.getItem('initPlay')):[]
        this.$sumaQtPlayer.preparePlayer(JSON.stringify(param), function (
          callback
        ) {
          let callbackObj = JSON.parse(callback)
          //====================
          if(callbackObj.status==="OK"){
            let recodeObj = [{
              playerIndex:callbackObj.data[0].playerIndex,
              groupIndex:callbackObj.data[0].groupIndex,
              channelId:callbackObj.data[0].channelId,
               business: {
                  isRecording: recodeFlag,
                  isDotting: false
                },
                player: {}
            }]
            vm.setQmlPlayerProperty(recodeObj)
            //
            if(!sessionStorage.getItem('monitorState')){
              //
              sessionStorage.setItem('monitorState',JSON.stringify(recodeObj))
            }else{
              let arr = JSON.parse(sessionStorage.getItem('monitorState'))
              let flag = true
              for(let item of arr){
                if(item.playerIndex === recodeObj[0].playerIndex){
                  flag = false
                  item.channelId = recodeObj[0].channelId
                  item.business = recodeObj[0].business
                  item.player = recodeObj[0].player
                  sessionStorage.setItem('monitorState',JSON.stringify(arr))
                }
              }
              if(flag){
                arr.push(recodeObj[0])
                sessionStorage.setItem('monitorState',JSON.stringify(arr))
              }
            }
            //
            var obj = {}
            obj.x =100
            obj.y =100
            obj.playerIndex = JSON.parse(callback).data[0].playerIndex
            obj.groupIndex = JSON.parse(callback).data[0].groupIndex
            obj.playerInfo = { 
              channelId,
              channelName,
              channelUrl,
              timeshiftTotalInSec
            }
            let flag = true//
            for(let item of vm.recordPlayUrlChange){
              if(item.playerIndex === obj.playerIndex){
                //playerIndex
                // item = JSON.parse(JSON.stringify(obj))
                for(let key in obj){
                  vm.$set(item,key,obj[key])
                }
                flag = false
                break
              }
            }
            if(flag){
              vm.recordPlayUrlChange.push(obj)
            }
            sessionStorage.setItem('initPlay',JSON.stringify(vm.recordPlayUrlChange))
          }
        });
    },
    //
    prepareQmlPlayer1(){
        let vm = this
        let param = []
        this.recordPlayUrlChange = sessionStorage.getItem('initPlay')?JSON.parse(sessionStorage.getItem('initPlay')):[]
        if(!this.recordPlayUrlChange.length){
          //
          return false
        }
        for(let item of this.recordPlayUrlChange){
          let obj = {}
          obj.x =100
          obj.y =100
          obj.playerIndex = item.playerIndex
          obj.groupIndex = item.groupIndex
          obj.playerInfo = item.playerInfo
          param.push(obj)
        }
        this.$sumaQtPlayer.preparePlayer(JSON.stringify(param), function (
          callback
        ) {
          
        });
    },
    //
    prepareQmlPlayer2(param){
        this.$sumaQtPlayer.preparePlayer(JSON.stringify(param), function (
          callback
        ) {
          
        });
    },
     //
     setQmlPlayerProperty(param){
        this.$sumaQtPlayer.setPlayerProperty(JSON.stringify(param), function (
          callback
        ) {
        });
     },
     //
     adjustQmlPlayer(param){
        this.$sumaQtPlayer.adjustPlayer(JSON.stringify(param), function (callback) {
        });
     },
     //video
     replaceVideoWithPlayer1(param){
       let vm = this
        vm.playerType = 'base'
        sessionStorage.setItem('playerType','base')
        this.$sumaQtPlayer.replaceVideoWithPlayer(JSON.stringify(param),function(callback){
         
        })
     },
    //
    destroyQmlPlayer(param){
     this.$sumaQtPlayer.destroyPlayer(JSON.stringify(param),function (callback) {
      });
    },
    //
    setVersionInfo(param){
     this.$sumaQtPlayer.setVersionInfo(JSON.stringify(param),function (callback) {
  
      });
    },
    //
    getVersion(){
      let obj={}
      api.getVersion(obj).then(res=>{
        res.result
        let param = {
          versionCode:0,
          versionName:res.result,
          versionDescription:''
        }
        this.setVersionInfo(param)
      })
    }
  }
}
</script>
<style>
  .planModal .ivu-modal-wrap[style]{position: static;}
  .planModal .ivu-modal{
    top: 0
  }
  .ivu-modal-mask{
    display: none
  }
  .monRoot{
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
  }
  .planModal .ivu-modal-body{
    height: 340px
  }
</style>


